import React from 'react';
import './book.scss';
import { NavLink } from 'react-router-dom';

const Book = props => {
  const book = props.book;

  return (
    <div className='book'>
      <div>{book.volumeInfo.title}</div>
      <div>{book.volumeInfo.subtitle}</div>
      <br />
    </div>
  );
};

export default Book;
